import React from 'react';
import Uptime from './uptime';

const App = () => {
  const { ApiKeys/*, SiteName*/ } = window.Config;
  // useEffect(() => {
  //   window.document.title = SiteName;
  // }, [SiteName]);
  return (
    <>
        <div className="mdui-card-content">
          <div id="uptime">
            {ApiKeys.map(i => (
              <Uptime key={i} apikey={i} />
            ))}
          </div>
        </div>
    </>
  );
}

export default App;
